import React from "react";
import { Link } from "react-router-dom";
const Header = () => {
  return (
    <header>
      <nav>
        <ul>
          <li>
            <Link to="/redux-toolkit-education">Home</Link>
          </li>
          <li>
            <Link to="redux-toolkit-education/counter">Counter</Link>
          </li>
          <li>
          <Link to="redux-toolkit-education/todos">Todos</Link>
          </li>
          <li>
          <Link to="redux-toolkit-education/posts">Posts</Link>
          </li>
          <li>
          <Link to="redux-toolkit-education/books">Books</Link>
          </li>
        </ul>
      </nav>
    </header>
  );
};

export default Header;
